import commongood from '../../components/common/commongood.vue'
import {
	mapState,
	mapActions
} from 'vuex'
export default {
	name: "",
	components: {
		commongood
	},
	props: {},
	data() {
		return {
			// scroll-view参数
			nameId: null,
			// 接收传过来的id
			littleId: null,
			// 接收传过来的请求对应的小类的id
			bigids: null
		}
	},
	methods: {
		...mapActions(['getSmallGoods', 'getSmallClassfy']),
		// 点击导航栏小类触发的方法
		changeSmall(sid) {
			this.littleId = sid
			this.getSmallGoods(this.littleId)
		}
	},
	mounted() {
		if (this.littleId !== null) {
			this.getSmallGoods(this.littleId)
		}
		if (this.bigids !== null) {
			this.getSmallClassfy(this.bigids)
		}
	},
	onLoad(option) {
		if (option.id) {
			this.littleId = option.id
			this.bigids = option.bigid
			this.nameId = "L2" + option.id
		}
	},
	filters: {

	},
	computed: {
		...mapState(['bigId', 'littleObj', 'smallObj']),
	},
	watch: {

	},
	directives: {

	}
}
